import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { LoadingController } from 'ionic-angular';
/**
 * Generated class for the ClasePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-clase',
  templateUrl: 'clase.html',
})
export class ClasePage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public apiProvider: ApiProvider, public loadingController:LoadingController) {

  this.claseData = {};

  }

  verificarReserva(idClase,idUsuario) {
    return this.apiProvider.verificarReserva(idClase,idUsuario)
    .then(data => {
      console.log(data);      
    });
  }

  	  ionViewDidLoad() {

	  
	    this.claseData=this.navParams.data;
 		console.log(this.claseData);

	  }
	  goReservar(){
	  this.navCtrl.push('ReservarPage');

	  }
	    
  		goTodasClases(){

  	    let loading = this.loadingController.create({content : "cargando..."});
	    loading.present();

	    this.verificarReserva(this.claseData.idClase, 1).then(()=>{
	        loading.dismissAll();
	    });

  }

}



